import styled from 'styled-components';
import { motion } from 'framer-motion';
import Icons from '../common/Icons';
import { useState, useRef, useEffect } from 'react';

// Add minimum and maximum width constraints
const MIN_SIDEBAR_WIDTH = 80;
const MAX_SIDEBAR_WIDTH = 400;
const DEFAULT_SIDEBAR_WIDTH = 280;
const COLLAPSED_WIDTH = 60;

const Sidebar = styled(motion.nav).attrs(props => ({
  'aria-label': 'Main navigation',
  'aria-expanded': props.$isOpen && !props.$isCollapsed
}))`
  position: fixed;
  left: 0;
  top: 0;
  bottom: 0;
  width: ${props => props.$isCollapsed ? `${COLLAPSED_WIDTH}px` : `${props.$width}px`};
  background: ${props => props.theme.colors.secondary};
  padding: 0;
  display: flex;
  flex-direction: column;
  border-right: 1px solid ${props => props.theme.colors.glass.border};
  backdrop-filter: blur(20px);
  z-index: ${props => props.theme.zIndex.modal};
  transition: width 0.3s ease;

  @media (max-width: 768px) {
    transform: translateX(${props => props.$isOpen ? '0' : '-100%'});
    width: ${props => props.$isCollapsed ? `${COLLAPSED_WIDTH}px` : '100%'};
    max-width: ${props => props.$isCollapsed ? `${COLLAPSED_WIDTH}px` : '320px'};
    transition: transform 0.3s ease-in-out, width 0.3s ease;
  }
`;

const Header = styled.div`
  padding: ${props => props.theme.spacing.lg} ${props => props.theme.spacing.xl};
  border-bottom: 1px solid ${props => props.theme.colors.glass.border};
  margin-bottom: ${props => props.theme.spacing.md};
  display: flex;
  justify-content: ${props => props.$isCollapsed ? 'center' : 'space-between'};
  align-items: center;
`;

const Logo = styled.h1`
  font-size: ${props => props.theme.typography.fontSize['2xl']};
  font-weight: ${props => props.theme.typography.fontWeight.bold};
  color: ${props => props.theme.colors.text.primary};
  margin: 0;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  display: ${props => props.$isCollapsed ? 'none' : 'block'};
`;

// Add toggle button component
const ToggleButton = styled.button`
  background: ${props => props.theme.colors.tertiary};
  border: none;
  color: ${props => props.theme.colors.text.primary};
  width: 32px;
  height: 32px;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  transition: all 0.2s ease;
  
  &:hover {
    background: ${props => props.theme.colors.text.accent};
    transform: scale(1.05);
  }
  
  svg {
    width: 16px;
    height: 16px;
    transition: transform 0.3s ease;
    transform: ${props => props.$isCollapsed ? 'rotate(180deg)' : 'rotate(0)'};
  }
  
  /* Mobile touch optimization */
  @media (max-width: 768px) {
    width: 44px;
    height: 44px;
    
    svg {
      width: 20px;
      height: 20px;
    }
  }
`;

// Add resizer component
const Resizer = styled.div`
  position: absolute;
  top: 0;
  right: -5px;
  width: 10px;
  height: 100%;
  cursor: ew-resize;
  z-index: ${props => props.theme.zIndex.modal + 1};
  display: ${props => props.$isCollapsed ? 'none' : 'block'};
  
  &:hover {
    background: ${props => props.theme.colors.text.accent}30;
  }
  
  &:active {
    background: ${props => props.theme.colors.text.accent}50;
  }
  
  /* Hide resizer on mobile for better touch experience */
  @media (max-width: 768px) {
    display: none;
  }
`;

const ScrollableContent = styled.div`
  flex: 1;
  overflow-y: auto;
  overflow-x: hidden;
  padding: 0 ${props => props.theme.spacing.lg};
  display: ${props => props.$isCollapsed ? 'none' : 'block'};
  
  /* Custom scrollbar */
  &::-webkit-scrollbar {
    width: 6px;
  }
  
  &::-webkit-scrollbar-track {
    background: ${props => props.theme.colors.glass.border};
    border-radius: 3px;
  }
  
  &::-webkit-scrollbar-thumb {
    background: ${props => props.theme.colors.text.accent};
    border-radius: 3px;
    
    &:hover {
      background: ${props => props.theme.colors.text.primary};
    }
  }
  
  /* Firefox scrollbar */
  scrollbar-width: thin;
  scrollbar-color: ${props => props.theme.colors.text.accent} ${props => props.theme.colors.glass.border};
`;

// Add floating expand button for collapsed desktop state
const ExpandButton = styled(motion.button)`
  position: fixed;
  top: 50%;
  left: ${COLLAPSED_WIDTH + 10}px;
  transform: translateY(-50%);
  width: 40px;
  height: 40px;
  border-radius: 50%;
  background: ${props => props.theme.colors.gradients.accent};
  border: none;
  color: ${props => props.theme.colors.text.primary};
  cursor: pointer;
  z-index: ${props => props.theme.zIndex.modal + 1};
  box-shadow: ${props => props.theme.shadows.neon};
  display: flex;
  align-items: center;
  justify-content: center;
  
  svg {
    width: 20px;
    height: 20px;
  }
  
  /* Hide on mobile */
  @media (max-width: 768px) {
    display: none;
  }
`;

const PartSection = styled.div`
  margin-bottom: ${props => props.theme.spacing.xl};
  
  &:last-child {
    margin-bottom: ${props => props.theme.spacing.lg};
  }
`;

const PartHeader = styled(motion.div)`
  display: flex;
  align-items: center;
  padding: ${props => props.theme.spacing.md} ${props => props.theme.spacing.lg};
  margin-bottom: ${props => props.theme.spacing.md};
  background: ${props => props.color}20;
  border: 1px solid ${props => props.color}40;
  border-radius: ${props => props.theme.borderRadius.lg};
  position: relative;
  overflow: hidden;
  
  &::before {
    content: '';
    position: absolute;
    left: 0;
    top: 0;
    bottom: 0;
    width: 4px;
    background: ${props => props.color};
  }
  
  .part-info {
    margin-left: ${props => props.theme.spacing.md};
    
    .part-title {
      font-size: ${props => props.theme.typography.fontSize.lg};
      font-weight: ${props => props.theme.typography.fontWeight.bold};
      color: ${props => props.theme.colors.text.primary};
      margin: 0 0 ${props => props.theme.spacing.xs} 0;
    }
    
    .part-subtitle {
      font-size: ${props => props.theme.typography.fontSize.sm};
      color: ${props => props.theme.colors.text.secondary};
      margin: 0;
    }
  }
  
  .part-icon {
    font-size: ${props => props.theme.typography.fontSize['2xl']};
    color: ${props => props.color};
  }
  
  .section-count {
    margin-left: auto;
    background: ${props => props.color};
    color: #121212;
    padding: ${props => props.theme.spacing.xs} ${props => props.theme.spacing.sm};
    border-radius: ${props => props.theme.borderRadius.full};
    font-size: ${props => props.theme.typography.fontSize.xs};
    font-weight: ${props => props.theme.typography.fontWeight.bold};
    font-family: ${props => props.theme.typography.fontFamily.mono};
  }
`;

const LockedIcon = styled.div`
  margin-left: auto;
  margin-right: ${props => props.theme.spacing.sm};
  color: ${props => props.theme.colors.text.secondary};
  opacity: 0.7;
  
  svg {
    width: 16px;
    height: 16px;
  }
`;

const NavList = styled.ul`
  list-style: none;
  margin: 0;
  padding: 0 ${props => props.theme.spacing.md};
  display: flex;
  flex-direction: column;
  gap: ${props => props.theme.spacing.sm};
`;

const NavItem = styled(motion.button)`
  display: flex;
  align-items: center;
  gap: ${props => props.theme.spacing.md};
  padding: ${props => props.theme.spacing.md};
  border: none;
  background: ${props => props.$active ? props.theme.colors.tertiary : 'transparent'};
  color: ${props => props.$active ? props.theme.colors.text.primary : props.theme.colors.text.secondary};
  border-radius: ${props => props.theme.borderRadius.lg};
  cursor: pointer;
  width: 100%;
  text-align: left;
  transition: all ${props => props.theme.transitions.normal};
  position: relative;
  opacity: ${props => props.$locked ? 0.7 : 1};
  
  &:hover {
    background: ${props => props.theme.colors.tertiary};
    box-shadow: ${props => props.theme.shadows.hover};
    transform: translateX(4px);
  }
  
  ${props => props.$active && `
    box-shadow: ${props.theme.shadows.accent};
    border-left: 3px solid ${props.theme.colors.text.accent};
  `}
  
  svg {
    width: 18px;
    height: 18px;
    color: currentColor;
    opacity: 0.8;
  }
  
  .section-title {
    flex: 1;
    font-size: ${props => props.theme.typography.fontSize.sm};
    line-height: 1.3;
  }
  
  .section-number {
    font-size: ${props => props.theme.typography.fontSize.xs};
    color: ${props => props.theme.colors.text.secondary};
    font-family: ${props => props.theme.typography.fontFamily.mono};
    opacity: 0.6;
  }
  
  /* Mobile touch optimization */
  @media (max-width: 768px) {
    min-height: 48px;
    padding: ${props => props.theme.spacing.lg};
    gap: ${props => props.theme.spacing.lg};
    
    &:hover {
      transform: none; /* Disable transform on mobile for better performance */
    }
    
    svg {
      width: 20px;
      height: 20px;
    }
    
    .section-title {
      font-size: ${props => props.theme.typography.fontSize.md};
      line-height: 1.4;
    }
    
    .section-number {
      font-size: ${props => props.theme.typography.fontSize.sm};
    }
  }
`;

const Footer = styled.div`
  padding: ${props => props.theme.spacing.lg} ${props => props.theme.spacing.xl};
  border-top: 1px solid ${props => props.theme.colors.glass.border};
  margin-top: ${props => props.theme.spacing.md};
  display: ${props => props.$isCollapsed ? 'none' : 'block'};
`;

const ProgressBar = styled.div`
  color: ${props => props.theme.colors.text.secondary};
  
  .progress-info {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: ${props => props.theme.spacing.sm};
    
    .progress-label {
      font-size: ${props => props.theme.typography.fontSize.sm};
      font-weight: ${props => props.theme.typography.fontWeight.semibold};
    }
    
    .progress-text {
      font-size: ${props => props.theme.typography.fontSize.xs};
      font-family: ${props => props.theme.typography.fontFamily.mono};
      color: ${props => props.theme.colors.text.accent};
    }
  }
  
  .progress-track {
    height: 6px;
    background: ${props => props.theme.colors.glass.border};
    border-radius: ${props => props.theme.borderRadius.full};
    overflow: hidden;
    position: relative;
  }
  
  .progress-fill {
    height: 100%;
    background: ${props => props.theme.colors.gradients.accent};
    width: ${props => props.$progress}%;
    transition: width ${props => props.theme.transitions.normal};
    border-radius: ${props => props.theme.borderRadius.full};
    position: relative;
    
    &::after {
      content: '';
      position: absolute;
      top: 0;
      right: -2px;
      width: 4px;
      height: 100%;
      background: ${props => props.theme.colors.text.accent};
      border-radius: 50%;
      box-shadow: 0 0 8px ${props => props.theme.colors.text.accent};
    }
  }
`;

const partConfigs = {
  1: {
    title: "Current AI Landscape",
    subtitle: "Market Overview & Opportunities",
    icon: "🌟",
    color: "#00ffe7"
  },
  2: {
    title: "CV & Portfolio Building",
    subtitle: "Recruiter Priorities & Project Strategy",
    icon: "📄",
    color: "#10b981"
  },
  3: {
    title: "Career Transition",
    subtitle: "PhD Analysis & Background Paths",
    icon: "🔄",
    color: "#f59e0b"
  },
  4: {
    title: "Resume Optimization",
    subtitle: "ATS & AI-Driven Screening",
    icon: "🚫",
    color: "#ef4444"
  },
  5: {
    title: "Networking & Referrals",
    subtitle: "LinkedIn Growth & Case Studies",
    icon: "🤝",
    color: "#8b5cf6"
  }
};

const Navigation = ({ sections, currentSection, setCurrentSection, isOpen, onClose, isContentUnlocked = false }) => {
  const [isCollapsed, setIsCollapsed] = useState(false);
  const [sidebarWidth, setSidebarWidth] = useState(DEFAULT_SIDEBAR_WIDTH);
  const progress = ((currentSection + 1) / sections.length) * 100;
  const sidebarRef = useRef(null);
  const resizerRef = useRef(null);

  // Group sections by part
  const groupedSections = sections.reduce((acc, section, index) => {
    const part = section.part;
    if (!acc[part]) {
      acc[part] = [];
    }
    acc[part].push({ ...section, index });
    return acc;
  }, {});

  // Handle click outside to close sidebar
  useEffect(() => {
    const handleClickOutside = (e) => {
      if (isOpen && sidebarRef.current && !sidebarRef.current.contains(e.target)) {
        onClose();
      }
    };

    document.addEventListener('mousedown', handleClickOutside);
    return () => {
      document.removeEventListener('mousedown', handleClickOutside);
    };
  }, [isOpen, onClose]);

  // Handle window resize
  useEffect(() => {
    const handleResize = () => {
      if (sidebarRef.current) {
        setSidebarWidth(sidebarRef.current.offsetWidth);
      }
    };

    window.addEventListener('resize', handleResize);
    return () => {
      window.removeEventListener('resize', handleResize);
    };
  }, [sidebarRef]);

  // Expose sidebar width and collapsed state to parent components
  useEffect(() => {
    // Create a custom event to notify about sidebar width changes
    const event = new CustomEvent('sidebarResize', { 
      detail: { 
        width: isCollapsed ? 60 : sidebarWidth,
        isCollapsed
      } 
    });
    document.dispatchEvent(event);
  }, [sidebarWidth, isCollapsed]);

  // Handle resizer drag
  const handleMouseDown = (e) => {
    e.preventDefault();
    const startX = e.clientX;
    const startWidth = sidebarWidth;

    const handleMouseMove = (e) => {
      const newWidth = Math.min(Math.max(startWidth + e.clientX - startX, MIN_SIDEBAR_WIDTH), MAX_SIDEBAR_WIDTH);
      setSidebarWidth(newWidth);
      
      // Dispatch event during drag to update layout in real-time
      const event = new CustomEvent('sidebarResize', { 
        detail: { 
          width: isCollapsed ? 60 : newWidth,
          isCollapsed
        } 
      });
      document.dispatchEvent(event);
    };

    const handleMouseUp = () => {
      window.removeEventListener('mousemove', handleMouseMove);
      window.removeEventListener('mouseup', handleMouseUp);
    };

    window.addEventListener('mousemove', handleMouseMove);
    window.addEventListener('mouseup', handleMouseUp);
  };

  // Handle toggle click - different behavior for mobile vs desktop
  const toggleSidebar = () => {
    // On mobile, close completely instead of collapsing
    if (window.innerWidth <= 768) {
      // Explicitly close the menu on mobile by setting isOpen to false
      // Instead of toggling, we want to ensure it's closed
      if (isOpen && typeof onClose === 'function') {
        onClose();
      }
    } else {
      // Desktop behavior: collapse/expand
      setIsCollapsed(prev => {
        const newCollapsedState = !prev;
        
        // Dispatch event for the collapse action
        const event = new CustomEvent('sidebarResize', {
          detail: {
            width: newCollapsedState ? COLLAPSED_WIDTH : sidebarWidth,
            isCollapsed: newCollapsedState
          }
        });
        document.dispatchEvent(event);
        
        return newCollapsedState;
      });
    }
  };

  return (
    <>
      <Sidebar
        ref={sidebarRef}
        initial={false}
        animate={{ x: 0 }}
        $isOpen={isOpen}
        $isCollapsed={isCollapsed}
        $width={sidebarWidth}
        onClick={(e) => e.target === e.currentTarget && onClose?.()}
      >
      <Header $isCollapsed={isCollapsed}>
        <Logo $isCollapsed={isCollapsed}>AI Careers 2025</Logo>
        <ToggleButton
          $isCollapsed={isCollapsed}
          onClick={(e) => {
            // For mobile devices, directly close the menu
            if (window.innerWidth <= 768) {
              e.stopPropagation();
              console.log("Mobile X button clicked, attempting to close menu");
              if (typeof onClose === 'function') {
                onClose();
              }
              // Extra effort to force close
              document.dispatchEvent(new CustomEvent('forceCloseMobileNav', { bubbles: true }));
            } else {
              toggleSidebar();
            }
          }}
          aria-label={
            window.innerWidth <= 768
              ? "Close navigation menu"
              : isCollapsed
                ? "Expand sidebar"
                : "Collapse sidebar"
          }
          className="mobile-nav-x-btn"
        >
          {window.innerWidth <= 768 ? (
            // Mobile: Show X icon
            <svg xmlns="http://www.w3.org/2000/svg" fill="none" viewBox="0 0 24 24" strokeWidth={2} stroke="currentColor">
              <path strokeLinecap="round" strokeLinejoin="round" d="M6 18L18 6M6 6l12 12" />
            </svg>
          ) : (
            // Desktop: Show collapse/expand icon
            <svg xmlns="http://www.w3.org/2000/svg" fill="none" viewBox="0 0 24 24" strokeWidth={2} stroke="currentColor">
              <path strokeLinecap="round" strokeLinejoin="round" d="M8 9l3 3-3 3m8-6l-3 3 3 3" />
            </svg>
          )}
        </ToggleButton>
      </Header>
      
      <ScrollableContent $isCollapsed={isCollapsed}>
        {Object.entries(groupedSections)
          .sort(([a], [b]) => Number(a) - Number(b))
          .map(([partNumber, partSections]) => {
            const partConfig = partConfigs[partNumber];
            if (!partConfig) return null;
            
            const isPartLocked = Number(partNumber) > 1 && !isContentUnlocked;
            
            return (
              <PartSection key={partNumber}>
                <PartHeader
                  color={partConfig.color}
                  initial={{ opacity: 0, x: -20 }}
                  animate={{ opacity: 1, x: 0 }}
                  transition={{ duration: 0.3 }}
                  style={{ opacity: isPartLocked ? 0.7 : 1 }}
                >
                  <div className="part-icon">{partConfig.icon}</div>
                  <div className="part-info">
                    <h3 className="part-title">Part {partNumber}</h3>
                    <p className="part-subtitle">{partConfig.subtitle}</p>
                  </div>
                  
                  {isPartLocked && (
                    <LockedIcon>
                      <svg xmlns="http://www.w3.org/2000/svg" fill="none" viewBox="0 0 24 24" strokeWidth={2} stroke="currentColor">
                        <path strokeLinecap="round" strokeLinejoin="round" d="M16.5 10.5V6.75a4.5 4.5 0 10-9 0v3.75m-.75 11.25h10.5a2.25 2.25 0 002.25-2.25v-6.75a2.25 2.25 0 00-2.25-2.25H6.75a2.25 2.25 0 00-2.25 2.25v6.75a2.25 2.25 0 002.25 2.25z" />
                      </svg>
                    </LockedIcon>
                  )}
                  
                  <div className="section-count">{partSections.length}</div>
                </PartHeader>
                
                <NavList role="tablist" aria-orientation="vertical">
                  {partSections.map((section) => {
                    const IconComponent = Icons[section.icon];
                    const isSectionLocked = section.part > 1 && !isContentUnlocked;
                    
                    return (
                      <li key={section.id}>
                        <NavItem
                          role="tab"
                          aria-selected={currentSection === section.index}
                          aria-controls={`section-${section.id}`}
                          $active={currentSection === section.index}
                          $locked={isSectionLocked}
                          onClick={() => setCurrentSection(section.index)}
                          whileTap={{ scale: 0.98 }}
                          whileHover={{ scale: 1.02 }}
                          title={isSectionLocked ? "This section is locked. Enter password to unlock." : undefined}
                        >
                          <IconComponent size="18px" aria-hidden="true" />
                          <span className="section-title">{section.title}</span>
                          
                          {isSectionLocked && (
                            <svg xmlns="http://www.w3.org/2000/svg" fill="none" viewBox="0 0 24 24" strokeWidth={1.5} stroke="currentColor" width="16" height="16">
                              <path strokeLinecap="round" strokeLinejoin="round" d="M16.5 10.5V6.75a4.5 4.5 0 10-9 0v3.75m-.75 11.25h10.5a2.25 2.25 0 002.25-2.25v-6.75a2.25 2.25 0 00-2.25-2.25H6.75a2.25 2.25 0 00-2.25 2.25v6.75a2.25 2.25 0 002.25 2.25z" />
                            </svg>
                          )}
                          
                          <span className="section-number">{section.index + 1}</span>
                        </NavItem>
                      </li>
                    );
                  })}
                </NavList>
              </PartSection>
            );
          })}
      </ScrollableContent>
      
      <Footer $isCollapsed={isCollapsed}>
        <ProgressBar $progress={progress}>
          <div className="progress-info">
            <span className="progress-label">Progress</span>
            <span className="progress-text">{currentSection + 1}/{sections.length}</span>
          </div>
          <div
            className="progress-track"
            role="progressbar"
            aria-valuemin="0"
            aria-valuemax="100"
            aria-valuenow={progress}
            aria-label="Presentation progress"
          >
            <motion.div 
              className="progress-fill"
              initial={{ width: 0 }}
              animate={{ width: `${progress}%` }}
              transition={{ duration: 0.5, ease: "easeOut" }}
            />
          </div>
        </ProgressBar>
      </Footer>
      
      <Resizer
        ref={resizerRef}
        $isCollapsed={isCollapsed}
        onMouseDown={handleMouseDown}
        aria-label="Resize sidebar"
      />
      </Sidebar>
      
      {/* Floating expand button for collapsed desktop state */}
      {isCollapsed && window.innerWidth > 768 && (
        <ExpandButton
          onClick={toggleSidebar}
          aria-label="Expand sidebar"
          initial={{ opacity: 0, scale: 0 }}
          animate={{ opacity: 1, scale: 1 }}
          exit={{ opacity: 0, scale: 0 }}
          whileHover={{ scale: 1.1 }}
          whileTap={{ scale: 0.9 }}
        >
          <svg xmlns="http://www.w3.org/2000/svg" fill="none" viewBox="0 0 24 24" strokeWidth={2} stroke="currentColor">
            <path strokeLinecap="round" strokeLinejoin="round" d="M13 5l7 7-7 7M5 5l7 7-7 7" />
          </svg>
        </ExpandButton>
      )}
    </>
  );
};

export default Navigation;